import React, {useEffect, useContext} from 'react';
import "./Mail.css";
import IconButton from "@material-ui/core/IconButton";
import {WatchLater} from "@material-ui/icons";
import {useHistory} from "react-router-dom";
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import MoveToInboxIcon from '@material-ui/icons/MoveToInbox';
import ErrorIcon from '@material-ui/icons/Error';
import DeleteIcon from '@material-ui/icons/Delete';
import EmailIcon from '@material-ui/icons/Email';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import LabelImportantIcon from '@material-ui/icons/LabelImportant';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import UnfoldMoreIcon from '@material-ui/icons/UnfoldMore';
import PrintIcon from '@material-ui/icons/Print';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import {selectOpenMail} from "./features/mailSlice.js";
import {useSelector} from "react-redux";
import {AppContext} from "./appContext.js";
import store from './app/store.js';
import {AddItem} from './AddItem.js';

function Mail() {
    useEffect(() =>{
        console.log("****rendering Mail Mail Mail****");
return () => console.log('****Mail unmount****');
});
    useEffect(() => console.log('****Mount of mail****'), [])
    const selectedMail = store.getState().mail.selectedMail;
    const history = useHistory();
   // const [name, setName] = useContext(AppContext);
    return (
        <div className={"mail"}>
            <div className="mail__tools">
                <div className="mail__toolsLeft">
                    <IconButton onClick={() => history.push({pathname:"/", state: {detail: 'mohamed'}})}>
                        <ArrowBackIcon/>
                    </IconButton>
                    <IconButton>
                        <MoveToInboxIcon/>
                    </IconButton>
                    <IconButton>
                        <ErrorIcon/>
                    </IconButton>
                    <IconButton>
                        <DeleteIcon/>
                    </IconButton>
                    <IconButton>
                        <EmailIcon/>
                    </IconButton>
                    <IconButton>
                        <WatchLater/>
                    </IconButton>
                    <IconButton>
                        <CheckCircleIcon/>
                    </IconButton>
                    <IconButton>
                        <LabelImportantIcon/>
                    </IconButton>
                    <IconButton>
                        <MoreVertIcon/>
                    </IconButton>

                </div>
                <div className="mail__toolsRight">
                    <IconButton>
                        <UnfoldMoreIcon/>
                    </IconButton>

                    <IconButton>
                        <PrintIcon/>
                    </IconButton>

                    <IconButton>
                        <ExitToAppIcon/>
                    </IconButton>
                </div>
            </div>

            <div className="mail__body">
                <div className="mail__bodyHeader">
                    <h2>{selectedMail?.subject}</h2>
                    <LabelImportantIcon className={"mail__important"}/>
                    <p>{selectedMail?.title}</p>
                    <p className={"mail__time"}>{selectedMail?.time}</p>
                </div>
                <div className="mail__message">
                    <p>{selectedMail?.description}</p>
                </div>
                <AddItem />
            </div>
        </div>
    )
}

export default Mail;