import React, {useState, createContext} from "react";

export const AppContext = createContext();

export const AppProvider = (props) => {
    const [movies, setMovies] = useState([
        {
            name: 'Harry Potter',
            price: '$10',
            id: 23124
        }
    ])
    return(
        <AppContext.Provider value={[movies, setMovies]}>
            {props.children}
        </AppContext.Provider>
    )
}