import React, {useState, useEffect, useContext} from 'react';
import {AppContext} from "./appContext.js";



export  const AddItem = () => {
    const [name, setName] = useState('');
    const [price, setPrice] = useState('');
    const [movies, setMovies] = useContext(AppContext);


    const updateName = (e) => {
        setName(e.target.value);
        console.log(name);
    }
    useEffect(() => {
        console.log("render add item");
        return () => console.log('unmount add item')
    })

    const updatePrice = (e) => {
        setPrice(e.target.value);
    }

    const addMovie = (e) => {
        e.preventDefault();
        setMovies(prevMovies => [...prevMovies, {name: name, price: price, id: Math.floor(Math.random() * 10)}])

    }

    return(
        <form onSubmit={addMovie}>
            <input type="text" name={"name"}  onChange={updateName}/>
            <input type="text" name={"price"} onChange={updatePrice} />
            <button type={"submit"}>Submit</button>d
        </form>
    )
}