import React, {useEffect, useState} from 'react';
import Header from './Header.js'
import './App.css';
import Sidebar from './Sidebar.js';
import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";
import Mail from "./Mail.js";
import EmailList from './EmailList.js';
import SendMail from "./SendMail";
import {useSelector} from "react-redux";
import {selectSendMessageIsOpen} from "./features/mailSlice";
import {login, userSliceSelector} from "./features/userSlice";
import store from "./app/store";
import Login from "./Login"
import {useDispatch} from "react-redux";
import {auth} from "./firebase";
import {AppProvider} from "./appContext"

function App() {
    const [sendMessageIsOpen, setSendMessageIsOpen] = useState(store.getState().mail.sendMessageIsOpen);

    // const user = store.getState().user.user;
    const [user, setUser] = useState(store.getState().user.user);
    console.log("useruser: ",user);
    const dispatch = useDispatch();

    store.subscribe(() => {
        setUser(store.getState().user.user);
        setSendMessageIsOpen(store.getState().mail.sendMessageIsOpen);
    });


    useEffect(() => {
        auth.onAuthStateChanged((user) => {
            if (user) {
                // the user is logged in
                dispatch(login({
                    displayName: user.displayName,
                    email: user.email,
                    photoUrl: user.photoURL,
                }))
            }
        })

    }, [])

// rendering of component
// recreation of inner function
    return (
        <Router>
            {
                !user ? (<Login />) :
                    (
                        <div className={"app"}>

                            <Header name={{name: 'moha'}}/>
                            <div className="app__body">
                                <Sidebar/>
                                <Switch>
                                    <Route path={"/mail"}>
                                        <AppProvider>
                                            <Mail/>
                                        </AppProvider>
                                    </Route>
                                    <Route exact path={"/"}>
                                        <AppProvider>
                                            <EmailList/>
                                        </AppProvider>

                                    </Route>
                                </Switch>
                            </div>
                            {sendMessageIsOpen && <SendMail/>}
                        </div>
                    )

            }
        </Router>
    );
}

export default App;


