import {createSlice} from "@reduxjs/toolkit";

const mineSlice = createSlice({
    name: 'mine',
    initialState: {
        arr: [],
        email: ''
    },
    reducers: {
        DELETE: (state, action) => {
            state.arr.pop();
        },
        ADD: function(state, action) {
            state.arr.push(action.payload.data);
        },
        MAIL: function(state, action) {
            state.email = action.payload;
        }
    }
});

export const {DELETE, ADD, MAIL} = mineSlice.actions;

export const mineReducer = mineSlice.reducer;

export const mineSliceStateSelector = (state) => (state.mine);
